.InputCity{
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 30%;
    height: 40px;
    padding-top: 1.5%;
    padding-left: 0.5%;
    input{
        height: 100%;
        width: 73.5%;
        font-size: 20px;
        border-radius: 5px;
        border: 1px solid rgba(0, 0, 0, 0);
        background-color: rgba(21, 21, 21, 0.84);
        color: rgba(132, 200, 124, 0.903);
        padding-left: 10px;
    }

    .Search{
        height: 110%;
        width: 23%;
        // background-color: rgba(2, 4, 25, 0.7);
        background-color: rgba(8, 137, 36, 0.772);
        color: rgb(231, 229, 229);
        font-size: 20px;
        cursor: pointer;
        border: none;
        border-radius: 5px;
    }
    .Search:hover{
        opacity: 0.9;
    }
    .Save{
        height: 110%;
        width: 23%;
        background-color: rgba(8, 137, 36, 0.705);
        color: rgb(255, 255, 255);
        font-size: 20px;
        cursor: pointer;
        border: none;
        border-radius: 5px;
    }
    .Save:hover{
        opacity: 0.9;
    }
}


@media screen and (max-width: 600px) {
.InputCity{
    width: 98%;
    padding-top: 0.1%;
    padding-left: 1%;
    input{
        height: 80%;
        width: 71%;
        font-size: 20px;
        border-radius: 5px;
        border: 1px solid rgba(0, 0, 0, 0);
    }
    .Search{
        height: 90%;
        width: 23%;
    }
    .Save{
        height: 90%;
    }
}
}