.Footer{
    position: absolute;
    padding: 15px 0;
    width: 100%;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.874);
            color: rgb(136, 202, 127);

.FooterLeft{
    width: 60%;
    text-align: center;
    h1{
        color: rgb(136, 202, 127);
    }
}
    .FooterLeftText{
        width: 60%;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
    }

    .FooterRight{
        display: flex;
        width: 10%;
        justify-content: space-between;
        font-size: 46px;
        .Inst:hover{
            color: rgba(234, 42, 3, 0.891);
            cursor: pointer;

        }
        .Facb:hover{
            color: rgb(42, 87, 211);
            cursor: pointer;


        }
        .Tg:hover{
            color: rgb(23, 161, 193);
            cursor: pointer;


        }
    }
}

@media screen and (max-width: 600px) {
    .Footer{
    position: relative;
    padding: 15px 0;
    width: 100%;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.874);
    color: rgb(136, 202, 127);

.FooterLeft{
    width: 55%;
    font-size: 14px;
    h1{
        font-size: 20px;
    }
}
    .FooterLeftText{
        width: 100%;
    }

    .FooterRight{
        width: 25%;
        font-size: 23px;
    }
}
}