.Menu{
    width: 30%;
    margin-top: 0.5%;
    ul{
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        font-size: 20px;
        font-weight: 400;
        li:hover{
            color: rgb(114, 189, 104);

            cursor: pointer;
        }
    }
}


@media screen and (max-width: 600px) {
    .Menu{
        display: none;
    }
}